import Vue from 'vue';
import VueRouter from 'vue-router';
import Login from '../components/Login';
import AdminPage from '../components/AdminPage';
import AddressesPage from '../components/pages/AddressesPage';
import AddressAddPage from '../components/pages/AddressAddPage';
import CarriersPage from '../components/pages/CarriersPage';
import CircuitLinksPage from '../components/pages/CircuitLinksPage';
import CircuitSpeedsPage from '../components/pages/CircuitSpeedsPage';
import CircuitTypesPage from '../components/pages/CircuitTypesPage';
import CircuitsPage from '../components/pages/CircuitsPage';
import DeviceManufacturersPage from '../components/pages/DeviceManufacturersPage';
import DevicesPage from '../components/pages/DevicesPage';
import LocationsPage from '../components/pages/LocationsPage';
import SupportOrganizationsPage from '../components/pages/SupportOrganizationsPage';

Vue.use(VueRouter);

const ifNotAuthenticated = (to, from, next) => {
    if (!localStorage.token) {
        next();
        return;
    }
    next({ name: 'adminPage' });
};

const ifAuthenticated = (to, from, next) => {
    if (localStorage.token) {
        next();
        return;
    }
    next({ name: 'login' });
};

export default new VueRouter({
    routes: [
        {
            name: 'adminPage',
            path: '/',
            component: AdminPage,
            beforeEnter: ifAuthenticated,
            children: [
                {
                    name: 'addresses',
                    path: 'addresses',
                    component: AddressesPage
                },
                {
                    name: 'addressAdd',
                    path: 'addresses/add',
                    component: AddressAddPage
                },
                {
                    name: 'carriers',
                    path: 'carriers',
                    component: CarriersPage
                },
                {
                    name: 'circuitLinks',
                    path: 'circuit_links',
                    component: CircuitLinksPage
                },
                {
                    name: 'circuitTypes',
                    path: 'circuit_types',
                    component: CircuitTypesPage
                },
                {
                    name: 'circuitSpeeds',
                    path: 'circuit_speeds',
                    component: CircuitSpeedsPage
                },
                {
                    name: 'circuits',
                    path: 'circuits',
                    component: CircuitsPage
                },
                {
                    name: 'deviceManufacturers',
                    path: 'device_manufacturers',
                    component: DeviceManufacturersPage
                },
                {
                    name: 'devices',
                    path: 'devices',
                    component: DevicesPage
                },
                {
                    name: 'locations',
                    path: 'locations',
                    component: LocationsPage
                },
                {
                    name: 'supportOrganizations',
                    path: 'support_organizations',
                    component: SupportOrganizationsPage
                }

            ]
        },
        {
            name: 'login',
            path: '/login',
            component: Login,
            beforeEnter: ifNotAuthenticated
        }

    ]
});
