<template>
    <b-row>
        <b-col cols="12">
            <h3>{{ title }}</h3>
            <b-table :items="items" striped>
                <template slot="_delete" slot-scope="data">
                    <b-button
                        v-if="!isDeletingItems[data.item.id]"
                        variant="link"
                        class="py-0 delete-button"
                        @click="onDelete(data.item.id)"
                    >
                        <i class="fa fa-trash"></i>
                    </b-button>
                    <preloader v-if="isDeletingItems[data.item.id]" size="20px"></preloader>
                </template>
            </b-table>
            <router-view></router-view>
        </b-col>
    </b-row>
</template>
<script>
import Backend from '../../services/backend';
import Preloader from '../Preloader';

const backend = new Backend();

export default {
    name: 'TemplateForGetPages',
    data() {
        return {
            items: [],
            isDeletingItems: {}
        };
    },
    components: {
        Preloader
    },
    props: [
        'title',
        'link'
    ],
    created() {
        this.loadValues();
    },
    methods: {
        onDelete(id) {
            this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: true });
            backend.deleteRequest(this._props.link + id)
                .then(() => {
                    this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: null });
                    this.loadValues();
                })
                .catch(() => {
                    this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: null });
                });
        },
        loadValues() {
            backend.getRequest(this._props.link)
                .then(response => {
                    this.items = response.data.map((item) => {
                        item._delete = 'delete';
                        return item;
                    });
                })
                .catch(error => {
                    console.log(error.response.status);
                });
        }
    }
};
</script>

<style scoped>
    .delete-button {
        color: #dc3545;
        line-height: 0;
    }
</style>
